import { BrowserRouter, Route } from 'react-router-dom';

import Home from './pages/Home';
import Inscrition from './pages/Inscrition';

function Routes() {
    return (
        <BrowserRouter>
            <Route path="/" exact component={Home} />
            <Route path="/inscrition" exact component={Inscrition} />
        </BrowserRouter>
    )
}

export default Routes;