export const CardBecause = styled.div`
    margin: 5rem;
    width: 95%;
    height: 20rem;
    background-image: linear-gradient(60deg, #343842, #4e5b78);
    border-radius: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    padding: 5rem 3rem;
    box-shadow: 0px 12px 80px rgba(97, 165, 205, 0.22), 0px 5.01331px 33.4221px rgba(97, 165, 205, 0.158148), 0px 2.68036px 17.869px rgba(97, 165, 205, 0.131144), 0px 1.50259px 10.0172px rgba(97, 165, 205, 0.11), 0px 0.798012px 5.32008px rgba(97, 165, 205, 0.0888564), 0px 0.332071px 2.21381px rgba(97, 165, 205, 0.061852);
    
    overflow: hidden;

    transition: 1s;

    &:hover {
        transform: scale(1.01);
    }

    @media(max-width: 768px) {
        width: 90%;
        height: 60rem;
        flex-direction: column;
        align-items: left;
    }
`;

