export const Expand = styled.a`
    color: #00B4DB;
    border-radius: 2.188rem;
    text-transform: uppercase;
    font: 500 1rem Montserrat;
    cursor: pointer;

    transition: 1s;

    &:hover {
        color: #00B4DB90;
        transform: scale(1.02);
    }

    @media(max-width: 768px) {
        font: 500 3rem Montserrat;
        margin-bottom: 1rem;
    }
`;

export const Card = styled.div`
    width: 23.5rem;
    height: 32rem;
    margin-left: 5rem;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    border-radius: 2.188rem;
    font: 500 1rem Montserrat;
    margin-top: 2rem;

    box-shadow: 0px 12px 80px rgba(255, 255, 255, 0.22), 0px 5.01331px 33.4221px rgba(255, 255, 255, 0.158148), 0px 2.68036px 17.869px rgba(255, 255, 255, 0.131144), 0px 1.50259px 10.0172px rgba(255, 255, 255, 0.11), 0px 0.798012px 5.32008px rgba(255, 255, 255, 0.0888564), 0px 0.332071px 2.21381px rgba(255, 255, 255, 0.061852);

    transition: 500ms;

    &:hover {
        transform: scale(1.01);
        box-shadow: 0px 12px 80px rgba(97, 165, 205, 0.22), 0px 5.01331px 33.4221px rgba(97, 165, 205, 0.158148), 0px 2.68036px 17.869px rgba(97, 165, 205, 0.131144), 0px 1.50259px 10.0172px rgba(97, 165, 205, 0.11), 0px 0.798012px 5.32008px rgba(97, 165, 205, 0.0888564), 0px 0.332071px 2.21381px rgba(97, 165, 205, 0.061852);
    }

    @media(max-width: 768px) {
        height: 70rem;
        width: 55rem;
        background-color: rgba(0, 0, 0, 0.5)
    }
`;

