function Inscrition() {
    const classes = useStyles();
    const history = useHistory();
    const [menuIsOpen, setMenuIsOpen] = useState(false);

    const [myState, setMyState] = useState({
        name: '',
        church: '',
        emailToMe: 'pastor.luisfilipe@gmail.com',
        email: '',
        phone: '',
      });

    const [state, setState] = useState({
        name: '',
        church: '',
        email: '',
        phone: '',
      });
    
      const [result, setResult] = useState(null);

      const sendEmails = (event) => {
          sendEmail(event);
          sendMyEmail(event);
      }
    
      const sendEmail = event => {
        event.preventDefault();
        api
          .post('/send', state)
          .then(response => {
            setResult(response.data);
            setState({
              name: '',
              church: '',
              email: '',
              phone: ''
            });
          })
          .catch(() => {
            setResult({
              success: false,
              message: 'Something went wrong. Try again later'
            });
          });
      };

      const sendMyEmail = event => {
        event.preventDefault();
        api
          .post('/send', myState)
          .then(response => {
            setResult(response.data);
            setState({
              name: '',
              church: '',
              email: '',
              phone: ''
            });
          })
          .catch(() => {
            setResult({
              success: false,
              message: 'Something went wrong. Try again later'
            });
          });
      };

    const onInputChange = event => {
        const { name, church, email, phone, value } = event.target;
    
        setState({
          ...state,
          [name]: value
        });
        setMyState({
            ...myState,
            [name]: value
        });
      };
    
    return (
    <>
        <Header>
            <Logo src={LogoECNPBranca} />
            <Hamburger style={{ position: 'absolute', fontSize: '1.8rem', right: '4rem', zIndex: '1500', }} onClick={() => setMenuIsOpen(!menuIsOpen)} isOpen={menuIsOpen} />
            <Drawer
                open={menuIsOpen}
                anchor="right"
                className={classes.drawer}
                onClose={() => setMenuIsOpen(false)}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerPaper}>
                    <MenuDrawer style={{ height: '100%' }}>
                        <Link style={{ textDecoration: 'none' }} to="/" onClick={() => setMenuIsOpen(false)}>
                            <Button className={classes.anchorMenu}>
                                Incio
                            </Button>
                        </Link>
                        <Link style={{ textDecoration: 'none' }} to="/#content" onClick={() => setMenuIsOpen(false)}>
                            <Button className={classes.anchorMenu}>
                                Contedo
                            </Button>
                        </Link>
                        <Link style={{ textDecoration: 'none' }} to="/#vantagens" onClick={() => setMenuIsOpen(false)}>
                            <Button className={classes.anchorMenu}>
                                Vantagens
                            </Button>
                        </Link>
                        <Link style={{ textDecoration: 'none' }} to="/#depoiments" onClick={() => setMenuIsOpen(false)}>
                            <Button className={classes.anchorMenu}>
                                Depoimentos
                            </Button>
                        </Link>
                        <Link style={{ textDecoration: 'none' }} to="/#inscrition" onClick={() => setMenuIsOpen(false)}>
                            <Button className={classes.buttonHeader}>
                                Inscrio
                            </Button>
                        </Link>
                    </MenuDrawer>
                </div>
            </Drawer>
            <MenuHeader>
              <Link style={{ textDecoration: 'none' }} to="/">
                <Button className={classes.anchorMenu}>
                    Incio
                </Button>
              </Link>
              <Link style={{ textDecoration: 'none' }} to="/#content">
                <Button className={classes.anchorMenu}>
                    Contedo
                </Button>
              </Link>
              <Link style={{ textDecoration: 'none' }} to="/#vantagens">
                <Button className={classes.anchorMenu}>
                    Vantagens
                </Button>
              </Link>
              <Link style={{ textDecoration: 'none' }} to="/#depoiments">
                <Button className={classes.anchorMenu}>
                    Depoimentos
                </Button>
              </Link>
              <Link style={{ textDecoration: 'none' }} to="/#inscrition">
                <Button className={classes.buttonHeader}>
                    Inscrio
                </Button>
              </Link>
            </MenuHeader>
        </Header>
        <ContainerInscrition className={classes.root}>
            <Form onSubmit={sendEmail}>
                <Typography className={classes.subTitle}>
                    Formulrio
                </Typography>
                <Typography className={classes.title}>
                    Inscrio para o ECNP
                </Typography>
                <Grid>
                    <TextField 
                        id="outlined-basic" 
                        label="Nome Completo" 
                        placeholder="Digite aqui"
                        variant="outlined"
                        type="text"
                        name="name"
                        color="primary"
                        value={state.name}
                        onChange={onInputChange}
                        InputLabelProps={{
                            classes: {
                                root: classes.cssLabel,
                                focused: classes.cssFocused,
                            }
                        }}
                        InputProps={{
                            classes: {
                                root: classes.cssOutlinedInput,
                                focused: classes.cssFocused,
                                notchedOutline: classes.notchedOutline
                            }
                        }}
                        autoComplete="off"
                    />
                    <TextField
                        id="outlined-basic" 
                        label="Igreja" 
                        placeholder="Digite aqui"
                        variant="outlined"
                        type="text"
                        name="church"
                        value={state.church}
                        onChange={onInputChange}
                        autoComplete="off"
                        InputLabelProps={{
                            classes: {
                                root: classes.cssLabel,
                                focused: classes.cssFocused,
                            }
                        }}
                        InputProps={{
                            classes: {
                                root: classes.cssOutlinedInput,
                                focused: classes.cssFocused,
                                notchedOutline: classes.notchedOutline
                            }
                        }}
                    />
                    <TextField
                        id="outlined-basic" 
                        label="E-mail" 
                        placeholder="Digite aqui"
                        variant="outlined"
                        type="text"
                        name="email"
                        value={state.email}
                        onChange={onInputChange}
                        autoComplete="off"
                        InputLabelProps={{
                            classes: {
                                root: classes.cssLabel,
                                focused: classes.cssFocused,
                            }
                        }}
                        InputProps={{
                            classes: {
                                root: classes.cssOutlinedInput,
                                focused: classes.cssFocused,
                                notchedOutline: classes.notchedOutline
                            }
                        }}
                    />
                    <TextField
                        id="outlined-basic" 
                        label="Telefone(Whatsapp)" 
                        placeholder="Digite aqui"
                        variant="outlined"
                        type="text"
                        name="phone"
                        value={state.phone}
                        onChange={onInputChange}
                        autoComplete="off"
                        InputLabelProps={{
                            classes: {
                                root: classes.cssLabel,
                                focused: classes.cssFocused,
                            }
                        }}
                        InputProps={{
                            classes: {
                                root: classes.cssOutlinedInput,
                                focused: classes.cssFocused,
                                notchedOutline: classes.notchedOutline
                            }
                        }}
                    />
                </Grid>
            <Button className={classes.buttonPay} onClick={sendEmails}>
                CONFIRMAR INSCRIO
            </Button>
            </Form>
        </ContainerInscrition>
    </>
    );
};

export default Inscrition;